      h1 Tintorera y lavandera
    .bg__1.mn.p-5(data-aos="zoom-in")
      .fondo-textil.p-5.mb-5
        .row.justify-content-center
          .col-lg-10
            .tarjeta.bg-white.p-4.m-5
              p.mb-0 Desde tiempos milenarios, en la moda se ha evidenciado el uso del color como un elemento cultural clave para identificar diversos modos de vivir, representados a travs de la vestimenta.
      p.mb-5(data-aos='fade-right') El siguiente video, expone aspectos y elementos que facilitan una comprensin ms amplia de los procesos de tintorera y lavandera y su importancia en la vida de las sociedades. Obsrvelo con atencin:
      figure
        .video
          iframe(width="560" height="315" src="https://www.youtube.com/embed/dF9-P2F36Do?si=wZI3Bxz2bPR_0yUv" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
        figcaption.mt-2 Video. Tintorera y lavandera, el color en las prendas y las culturas
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        p.mb-0 Se define tintorera al #[span.color-primario.fw-bold conjunto de procesos qumicos que permiten al sustrato, adquirir un color, de acuerdo con el requerimiento final] (p. 3). Estas acciones encaminadas a darle color a una materia prima en crudo se generaron a travs de diferentes tcnicas rudimentarias, haciendo uso de tintes naturales que, a su vez, se convirtieron en colorantes sintticos al desarrollarse la qumica. Asimismo, la lavandera es un proceso inherente a la tintorera, por el uso de diversas tcnicas en la que se encuentra presente.  
      .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/temas/tema2/2.png', alt='')
    p.mb-5(data-aos='fade-right') De esta manera, se han definido cuatro elementos fundamentales para llevar a cabo el proceso de tintorera:
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Tabla 1 
          span Elementos clave para el proceso de tintorera
        .tabla-a.color-acento-botones.mb-5
          table
            tbody(style="border: 2px solid #FF6E33")
              tr
                td.fw-bold.text-center(style="background-color: #FFAD8B; width: 30% ")  Sustrato

                td(style="background-color: #FEE7DD ") Se define como la materia prima que ser teida, la cual se puede presentar como fibras, hilos, tejidos, entre otros. Dependiendo de sus particularidades, se puede evidenciar un teido exitoso, es decir, su estructura qumica, grado de blancura, intensidad de torsin, factor de cobertura, etc.
              tr
                td.fw-bold.text-center(style="background-color: #FFAD8B; width: 30% ") Insumos 
                td.bg-white Se refiere a los colorantes o blanqueadores pticos. Estos elementos, ya sean qumicos o naturales, son los que actan directamente en el proceso de tintorera para realizar la impresin de color.
              tr
                td.fw-bold.text-center(style="background-color: #FFAD8B; width: 30% ") Maquinaria 
                td(style="background-color: #FEE7DD ") La tecnologa en maquinaria utilizada para llevar a cabo los procesos de tintorera es indispensable para lograr los resultados esperados. Segn el tipo de sistema de trabajo, se determina si se desarrollar a travs de un sistema continuo, discontinuo o semicontinuo. 
              tr
                td.fw-bold.text-center(style="background-color: #FFAD8B; width: 30% ") Factor humano
                td.bg-white El control y desarrollo del proceso de tintorera define el factor humano como el elemento ms importante, puesto que est en capacidad de realizar directamente acciones operativas, medias o directivas, en funcin del resultado esperado. 
    #t_2_1.titulo-segundo.color-extra(data-aos='fade-right')
      h2 2.1 Tratamientos previos
    p.mb-5(data-aos='fade-right') Los tratamientos previos al proceso de tintorera suponen la preparacin de la materia prima para ser intervenida. Esto, segn una serie de variables y la necesidad con la cual ser fabricado el producto textil para ofrecerlo al cliente final; as es como se establecen los mtodos a utilizarse.
    .row.justify-content-center.mb-5
      .col-lg-4.col-7.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema2/3.png', alt='')
      .col-lg-8
        p Dentro de una gran variedad de tratamientos previos, se destacan los siguientes:
        ul.lista-ul--color.ms-4
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Gaseado o chamuscado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Termo fijado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Desengomado o desencolado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Descrudado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Blanqueo
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Mercerizado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | <em>Antipilling</em>
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Carbonizado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Decorticado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Lavado
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Hidroextraccin
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Apertura de gneros de punto tubulares
        .tarjeta.color-secundario.p-3
          .row.justify-content-around.align-items-center
            .col-auto
              img(src="@/assets/curso/temas/adjunto.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h4.mb-1 Tratamientos Previos
                  p.mb-0.text-small Ample informacin sobre cada tratamiento previo del textil, en el proceso de teido, leyendo con suma atencin el Anexo_1_TratamientosPrevios.
                .col-sm-auto
                  a.boton.color-acento-botones(:href="obtenerLink('downloads/Anexo_1_TratamientosPrevios.pdf')" target="_blank" type="application/pdf")
                    span Descargar
                    i.fas.fa-file-download
    .bloque-texto-a.color-acento-contenido.p-4.p-md-5.mb-5(data-aos="zoom-in")
      .row.m-0.align-items-center.justify-content-between
        .col-lg-3.mb-4.mb-lg-0.order-2
          img(src='@/assets/curso/temas/tema2/4.png', alt='')
        .col-lg-9.order-1
          .bloque-texto-a__texto.p-4
            p Adems de los distintos tratamientos previos mencionados, se resalta el secado, es un elemento muy importante en el proceso textil. Ya sea en la preparacin o la aplicacin del tinte en un material, es necesario realizar un secado posterior. Dependiendo de las caractersticas de las fibras, la tela absorber grandes o pequeas cantidades de agua, de esta forma, el lquido que se encuentra en la superficie se puede eliminar mecnicamente.
    .row.justify-content-center.mb-5
      .col-lg-3.col-7.mb-lg-0.mb-3(data-aos="fade-right"): img.d-lg-block.d-none(src='@/assets/curso/temas/tema2/5.png', alt='')
      .col-lg-9(data-aos="fade-left")
        p Por otro lado, para el agua que se encuentra en las fibras hinchadas, se debe llevar a cabo un proceso de secado, #[span.color-acento-contenido que por lo general se realiza utilizando aire caliente, con el fin de evaporar el agua alojada en el material.]
        .tarjeta-clip.p-4(style="background-color: #fee7dd ").mb-4
          img(src='@/assets/curso/temas/tema2/6.svg', alt='' style="width: 79px").me-4
          p Para elegir la tcnica de secado ms adecuada, se deben tener en cuenta diversos factores que atiendan a las distintas necesidades, como los costos, capacidad de evaporacin, agua y energa requerida, etc. Adems, el tejido solo debe estar en la unidad de secado el tiempo justo para eliminar la humedad. 
        p De esta manera, se identifican los siguientes sistemas de secado:
        ul.lista-ul--color.ms-4
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Secado por conveccin de calor.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Secado por contacto con superficies metlicas calientes.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Secado por radiacin infrarroja.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Secado por microondas u ondas de alta frecuencia.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Secado por combustin.
        .tarjeta.color-secundario.p-3
          .row.justify-content-around.align-items-center
            .col-auto
              img(src="@/assets/curso/temas/adjunto.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h4.mb-1 Sistemas de secado 
                  p.mb-0.text-small Detalle cada uno de los sistemas de secado, estudiando el Anexo_2_SistemasDeSecado. 
                .col-sm-auto
                  a.boton.color-acento-botones(:href="obtenerLink('downloads/Anexo_2_SistemasDeSecado.pdf')" target="_blank" type="application/pdf")
                    span Descargar
                    i.fas.fa-file-download
    Separador
    #t_2_2.titulo-segundo.color-extra(data-aos='fade-right')
      h2 2.2  Teido
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3(data-aos="fade-right")
        p El proceso de teido se basa en la capacidad que tiene un tejido para retener los colorantes que se aplican mediante distintos mtodos. El proceso de teido textil se realiza en sustratos como: fibras, hilos, telas y prendas, a travs de diferentes fases; de esta forma, dependiendo del mismo sustrato y de la etapa del proceso textil en que se aplique el teido, es como se puede evidenciar la tcnica ms adecuada para alcanzar el nivel de absorcin del color ptimo.
          br
          br
          | Este se realiza de tres maneras:
      .col-lg-5.col-8(data-aos="fade-left"): img.d-lg-block.d-none(src='@/assets/curso/temas/tema2/7.png', alt='')
    .bg-middle.p-5.pb-0.mb-5(data-aos="zoom-in")
      .contenido
        .row.justify-content-center
          .col-lg-3.mb-lg-0.mb-4.col-6
            img(src='@/assets/curso/temas/tema2/8.svg', alt='' )
          .col-lg-9
            .row.justify-content-center.mb-3
              .col-lg-4.mb-lg-0.mb-3
                .bg-white.p-4.h-100
                  h4
                    span.color-acento-contenido Adsorcin fsica
                  p Esta sucede cuando el colorante se adhiere con fuerza al textil, lo que le permite resistir a posteriores procesos de lavado. 
              .col-lg-4.mb-lg-0.mb-3
                .bg-white.p-4.h-100
                  h4
                    span.color-acento-contenido Adsorcin mecnica
                  p Se basa en la integracin de materiales y pigmentos insolubles en la fibra.
              .col-lg-4.mb-lg-0.mb-3
                .bg-white.p-4.h-100
                  h4
                    span.color-acento-contenido Reaccin en fibra
                  p Se refiere al proceso en el que los iones y molculas del colorante, al introducirse en las fibras, se conectan gracias a enlaces qumicos covalentes.
            p La calidad de un buen teido est sujeta a diversas condiciones, que se pueden observar al realizar el proceso o en pruebas posteriores. Es de tener en cuenta que, segn el material a trabajar, se determinar la mquina que denote una proteccin del sustrato, genere repetitividad de los resultados y establezca un costo adecuado del proceso. 
    p.text-center Para realizar el proceso de teido se han especificado dos mtodos estndar, como se muestra en la siguiente tabla:
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="zoom-in")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Tabla 2
          span  Mtodos estndar del proceso de teido
        .tabla-a.color-acento-botones.mb-5
          table
            thead(style="border-top: 0px")
              tr(style='background-color: #FF6E33')
                th(style='width: 20%') Mtodo
                th Definicin
                th Ilustracin
            tbody
              tr
                td.text-center(style='width: 20%') Tintura por agotamiento / Sistema discontinuo
                td Se disuelve el colorante en un bao de agua, se sumerge el material y se retira una vez se evidencia la impregnacin homognea del tinte en la fibra. Despus, se procede a lavar para eliminar los residuos. Al finalizar, se contina con el secado.
                  br
                  br
                  |En este sistema, se establece que las molculas del colorante pasan por etapas de difusin, adsorcin y fijacin.
                td 
                  img(src='@/assets/curso/temas/tema2/10.svg', alt='Ilustracin de molculas de tinte diluidas y luego penetradas en la fibra de un tejido.')
              tr
                td.text-center(style='width: 20%') #[em Foulard]ado / Sistema continuo o semicontinuo
                td En este proceso, se utilizan medios mecnicos que permiten agregar el tinte sobre la tela homogneamente, lo cual hace que todo el textil se impregne. Una vez que ha penetrado el color en todo el material, se puede determinar que ya qued fijado. Para concluir, se procede a lavar.
                td
                  img(src='@/assets/curso/temas/tema2/11.svg', alt='Ilustracin grfica que ejemplifica el foulardado de material textil: tejido seco, tejido impregnado, compresin.')
    .row.justify-content-center.mb-5
      .col-lg-5.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema2/12.png', alt='Ilustracin del proceso de un tejido impregnado y cmo se comprime la fibra homogneamente en la zona de contacto.')
      .col-lg-7(data-aos="fade-left")
        p Las operaciones que se llevan a cabo en los dos sistemas son: 
        ul.lista-ul--color.ms-4
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Agregar el colorante en el agua.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Lograr homogeneidad del tinte al penetrar en la fibra.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Fijar el colorante.
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            | Concluir con el lavado del textil.
    .titulo-icono.mb-4
      .titulo-icono__texto: h4.text-white.mb-0 Teido por sistema discontinuo y semicontinuo
    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        p Este sistema se explica desde la teora del por agotamiento, la cual se enfoca en la reaccin qumica que se genera entre el material textil y el colorante que se agrega, donde se tiene en cuenta la velocidad del proceso y el balance de este.  
          br
          br
          | Para ello, se ha determinado la siguiente frmula:
        .row.justify-content-center.mb-5
          .col-5: img(src='@/assets/curso/temas/tema2/15.svg', alt='Imagen de frmula que sugiere que, al impregnar la fibra de colorante y tener determinado resultado, se confirma o invierte el proceso.')
      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-left"): img(src='@/assets/curso/temas/tema2/13.png', alt='')
    p.mb-5(data-aos='fade-right') Como es entendible, la qumica juega un papel fundamental en el proceso de teido; para conocer el proceso en que una fibra es impregnada por el colorante, se han establecido cuatro etapas que explican este sistema:
    .tarjeta.bg-card.mb-4.p-4(data-aos="zoom-in")
      LineaTiempoC.color-acento-botones(text-small)
        .row(titulo="Primera fase").p-4
          .col-md-9.mb-4.mb-md-0
            h5
              span.color-acento-contenido Disolucin y dispersin del colorante 
            p En esta etapa, se define si los colorantes son solubles o dispersables en agua y se pueden presentar de forma micelar o en estructura de micropolvo.
          .col-md-2.col-6: img(src='@/assets/curso/temas/tema2/16.svg', alt='')
        .row(titulo="Segunda fase").p-4
          .col-md-9.mb-4.mb-md-0
            h5
              span.color-acento-contenido Adsorcin 

            p En esta etapa, el colorante se integra en la fibra por el proceso de adsorcin, adems, factores como la temperatura, el pH y la afinidad determinan el equilibrio de las reacciones.
          .col-md-2.col-6: img(src='@/assets/curso/temas/tema2/17.svg', alt='')
        .row(titulo="Tercera fase").p-4
          .col-md-9.mb-4.mb-md-0
            h5
              span.color-acento-contenido Difusin 
            p Esta es la etapa ms lenta del proceso, donde el colorante es adsorbido por la superficie del material textil, al impregnarse a travs de sus fibras para distribuirse de forma homognea y, posteriormente, fijarse. De esta manera, se determina la calidad del teido; para ello, se debe tener en cuenta la cristalinidad de la fibra, el tamao molecular del colorante, la fuerza de la afinidad entre el colorante y la fibra, adems de la temperatura de teido.
          .col-md-2.col-6: img(src='@/assets/curso/temas/tema2/18.svg', alt='')
        .row(titulo="Cuarta fase").p-4
          .col-md-9.mb-4.mb-md-0
            h5
              span.color-acento-contenido Migracin 
            p En esta ltima fase, se invierte el proceso, al fundirse el colorante en las capas externas de la fibra y luego migrar hacia las reas donde hay una menor concentracin, lo que permite igualar el color en el material.
          .col-md-2.col-6: img(src='@/assets/curso/temas/tema2/19.svg', alt='')
    p.mb-5(data-aos='fade-right') Al observar las diferencias entre los dos sistemas de teido, se evidencia que comparten similitudes en varias operaciones.  Asimismo, existen diversos elementos que se deben tener en cuenta, ya sea en el sistema discontinuo o semicontinuo.
      br
      br
      |Tales elementos son:
    h3(data-aos="fade-right")
      span.color-acento-contenido Cantidad de productos por aadir
    p.mb-5(data-aos='fade-right') Para entender cul es la cantidad exacta de producto que se debe agregar al textil para un proceso ptimo de teido. Por ello, las recetas de tintorera estn determinadas por:
    .row.justify-content-center.mb-5
      .col-lg-4.mb-lg-0.mb-3(data-aos="zoom-in")
        .tarjeta.p-4(style="background-color: #F5E6E9").h-100
          p #[b a. Volumen de bao:] concentracin en gramos del producto por cada kilo. Su frmula es: 
            br
            br

            | #[span.color-acento-contenido.fst-italic   ()] #[span.color-acento-contenido.fw-bold = Concentracin del producto (g/l) . Volumen del bao (l)]
            br
            br
            |#[b Donde:] 
            br
            |#[b (g/l):] gramos del producto por cada litro de bao 
            br
            |#[b (l):] Litro de bao
          
      .col-lg-4.mb-lg-0.mb-3(data-aos="zoom-in")
        .tarjeta.p-4(style="background-color: #F5E6E9").h-100
          p #[b b. Peso del material:] determina cuntas unidades de peso del producto se necesitan por 100 unidades de peso del sustrato. La frmula es: 
          img(src='@/assets/curso/temas/tema2/20.svg', alt='Frmula que indica que el peso del producto en es igual al valor porcentual por el peso del sustrato sobre cien.')
          p #[b Donde:]
            br
            |% spm: valor porcentual sobre el peso del material

          
      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-left"): img(src='@/assets/curso/temas/tema2/21.png', alt='')
    h3(data-aos="fade-right")
      span.color-acento-contenido Relacin de bao (R/B)
    p.mb-5(data-aos='fade-right') Se refiere a la cantidad de bao utilizado para teir una cantidad definida del material textil. De esta manera, se entiende que, si en una receta hay mayor relacin de bao, se requiere ms agua blanda, energa y con ello se incrementa el peso de los productos y la cantidad de efluentes. 
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema2/22.png', alt='')
      .col-lg-6(data-aos="fade-left")
        .tarjeta.p-3(style="background-color: #F5E6E9").h-100
          p.mb-0 #[b Ejemplo:] 
            br
            br
            |Si se carga la mquina con tela seca y se tienen los siguientes datos: 300 kg es el peso de tela y el R/B: 1:10. 
            br
            br
            |El volumen final ser: 
            br
            br
            | #[span.color-acento-contenido.fw-bold.fst-italic 300 kg x 10 l/kg = 3000 l]
    h3(data-aos="fade-right")
      span.color-acento-contenido Tiempo de rotacin
    p Se refiere al tiempo que una cuerda demora en dar la vuelta dentro de una mquina. De esta forma, al determinar la velocidad en que se mueve la cuerda, es posible conocer cunto tiempo tarda la tela en rotar. 
      br
      br
      |La velocidad del torniquete y la velocidad de la cuerda deben ser muy similares, para asegurar que no se generen tensiones sobre el sustrato.
    .row.justify-content-center.mb-5
      .col-6: img(src='@/assets/curso/temas/tema2/23.svg', alt='Frmula que indica que la velocidad de la cuerda es igual a la longitud de cuerda (en metros) sobre el tiempo por vuelta, medido en minutos.')
    p Como dato adicional, es bueno indicar que la longitud de la cuerda puede calcularse fcilmente si conocemos el rendimiento del tejido y el peso de la tela. 
        br
        br
        |#[span.color-acento-contenido.fw-bold.fst-italic Longitud de la cuerda m = Rendimiento (m kg) . Peso (kg)]
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10(data-aos="zoom-in")
        .tarjeta.p-5(style="background-color: #F5E6E9").h-100
          .row.justify-content-center
            .col-lg-8.mb-lg-0.mb-3
              p.mb-4 #[b Ejemplo]
