      .titulo-icono__texto: h4.text-white.mb-0 Teido por sistema continuo
    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-3
        p Por otro lado, en el teido por sistema continuo, adems del volumen de bao establecido anteriormente en el sistema discontinuo, se tiene en cuenta el porcentaje de retencin (#[em Pick up]) de un tejido. Esto hace referencia al peso que es capaz de retener el textil al ser impregnado por el tinte y pasado por el #[em foulard].
          br
          br
          | Adems, el #[em pick up] est inversamente relacionado con la presin de exprimido en los rollos del #[em foulard], es decir, el #[em pick up] disminuye a mayor presin de exprimido y aumenta a menor presin de exprimido. 
          br               
          br
          | La siguiente tabla detalla algunas frmulas aplicables al teido por sistema continuo:
