    Separador 
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.3  Insumos
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema2/32.png', alt='')
      .col-lg-8(data-aos="fade-left")
        p Para iniciar con el proceso de tintorera, es necesario conocer los insumos aptos para dicha ejecucin. En este apartado, se realiza la descripcin y reconocimiento de las propiedades de cada uno de ellos. Como punto de partida, se inicia con el insumo ms utilizado en este rubro, el H2O, ms conocido como agua, entendiendo su gran importancia y profundizando en diferentes tipos de dureza, segn variantes en disoluciones; continuando con la explicacin de productos qumicos, auxiliares, colorantes, enzimas y blanqueadores ptimos. 
          br  
          br
          | Por consiguiente, se obtiene la informacin y recursos precisos para llevar a cabo procesos de tinturado y lavandera en la industria de la moda.
    .mn.p-5(style="background-color: #fee7dd " data-aos="fade-right").mb-5
      .titulo-icono.mb-4
        .titulo-icono__texto: h4.text-white.mb-0 El agua
      .row.justify-content-center.mb-5
        .col-lg-8.mb-lg-0.mb-3
          p El agua es conocida, en el sector de tintoreros, como el disolvente universal, ya que es el insumo ms utilizado en el proceso del color, comparado con los dems; por ende, su calidad y la cantidad a utilizar son analizadas de manera concisa y detallada, para disolver una gran variedad de otras sustancias en la receta de colorimetra. El agua cumple con diferentes roles, actuando como medio de transporte mecnico, de transferencia de calor, intercambio qumico y como mecanismo disolutor.
            br
            br
            | A continuacin, se analizan diferentes conceptos que son de suma importancia para emplear el H2O como disolvente.
        .col-lg-4.col-8: img(src='@/assets/curso/temas/tema2/33.png', alt='')
      .row.justify-content-center.mb-4
        .col-lg-9(data-aos="fade-right")
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Tabla 4
            span Conceptos generales del agua
          .tabla-a.color-acento-botones
            table
              caption Nota: tomado de Lockun (2012). 
              thead(style="border-top: 0px")
                tr(style='background-color: #FF6E33')
                  th(style='width: 20%') Concepto
                  th Descripcin
              tbody.bg-white
                tr
                  td.text-center(style='width: 20%') Dureza del agua
                  td Es la concentracin de compuestos minerales de cationes alcalinotrreos (grupo 2) en una muestra especfica de agua (particularmente, magnesio y calcio). Su frmula es:
                    br
                    br

                    |(mg/l de CaCO3) = 2,50 [Ca++] + 4,16 [Mg++]
                tr
                  td.text-center(style='width: 20%') Tipos de dureza
                  td
                    p #[b Dureza temporal:] tambin conocida como dureza de carbonatos, ya que se produce a partir de hidrgeno carbonatos, ms conocidos como bicarbonatos.
                      br
                      br
                      |#[b Dureza permanente:] est se da por la presencia de sulfatos y/o cloruros de calcio y de magnesio en el agua. Se debe tener presente que son mucho ms solubles estas sales.
                tr
                  td.text-center(style='width: 20%') Medidas de la dureza del agua
                  td -	mg CaCO3/l o ppm de CaCO3

                    br
                    br
                    |Miligramos de carbonato de calcio (CaCO3) en un litro de agua; esto es equivalente a ppm de CaCO3.
                    br
                    |-	Grado alemn (Deutsche Hrte,  dH)

                    br
                    br
                    |Equivale a 17,9 mg CaCO3/l de agua.
                tr
                  td.text-center(style='width: 20%') Eliminacin de la dureza
                  td Son procesos donde se realiza un ablandamiento de aguas. Existen diferentes maneras:
                    br
                    br
                    |-	Utilizando el carbonato de sodio (o de potasio) y cal. 
                    br
                    br
                    |-	La descalcificacin mediante resinas (zeolitas) de intercambio inico. 
                tr
                  td.text-center(style='width: 20%') Agua dura
                  td Es utilizada en procesos que no requieren mucha sutileza en procesos de tintorera. Cabe resaltar que algunos casos de teido tambin son usados. 
      .row.justify-content-center.mb-4
        .col-lg-9(data-aos="zoom-in")
          .tarjeta-clip.p-4(style="background-color: #d9fbfc ").mb-4
            img(src='@/assets/curso/temas/tema2/34.svg', alt='' style="width: 79px").me-4
            p #[span.h4.color-acento-contenido Clasificacin de dureza del agua!]
              br
              br
              |En cada industria, la dureza del agua se considera ptima dependiendo de su rubro. En la industria textil, especficamente, cuando se refiera a tintorera, se considera una dureza mxima en 6 grados alemanes, pero es preferible una leve dureza con un valor lmite de 3 grados alemanes.
      p A continuacin, se muestra un cuadro general con la clasificacin de dureza del agua:
      .row.justify-content-center.mb-5
        .col-lg-10.mb-3(data-aos="fade-right")
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Tabla 5 
            span  Clasificacin de dureza
