            h4
              span.color-acento-contenido Qu sucede cuando el agua es dura?
            ul.lista-ul--color
              li.d-flex.mb-1
                i.fas.fa-tshirt(style='color: #FF6E33')
                | Precipita los colorantes cidos y bsicos.
