                i.fas.fa-tshirt(style='color: #FF6E33')
                | Les resta las propiedades de detergente a los jabones. 
              li.d-flex.mb-1
                i.fas.fa-tshirt(style='color: #FF6E33')
                | En calderas, calentadores y lavadoras, en sus tuberas, puede generar incrustaciones de carbonatos. 
              li.d-flex.mb-1
                i.fas.fa-tshirt(style='color: #FF6E33')
                | En las tuberas, puede provocar obstrucciones, que se pueden retirar con cidos dbiles, tales como el actico, ctrico, entre otros.
    .titulo-icono.mb-4(data-aos="fade-right")
