    p.fw-bold(data-aos="fade-right") Los productos qumicos se dividen en cuatro grupos. Segn el tipo de compuesto, estos grupos son: 
    .row.justify-content-center.mb-5
      .col-lg-5.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img.d-lg-block.d-none(src='@/assets/curso/temas/tema2/35.png', alt='' style="width: 100%")
      .col-lg-7(data-aos="fade-left")
        AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta tarjeta--red")
          div(titulo="Los cidos")
            p Los cidos, en el momento que se disuelven en agua, la solucin obtenida alcanza un pH menor que 7, debido a que se ha producido una actividad de catin hidronio ms alta que el agua pura. Entre los cidos ms fuertes, se tiene el sulfrico, el clorhdrico y el ntrico, y como los cidos ms dbiles, el actico, frmico y oxlico.
          div(titulo="Los lcalis")
            p Los lcalis son xidos, hidrxidos y carbonatos de metales alcalinos. Se debe tener presente que jams se pueden mezclar con los cidos, ya que son opuestos. Entre los ms fuertes, se encuentra el hidrxido de sodio (soda custica) y entre los dbiles, el carbonato de sodio y el amonaco. Este grupo de lcalis se aplican en muestras, segn recetas ya determinadas, para realizar descrude, blanqueo y teido.
          div(titulo="Las sales")
            p Las sales estn constituidas por cationes (iones con carga positiva) enlazados a aniones (iones con carga negativa), y son usadas generalmente en tareas de tintura. Las ms utilizadas son: sulfato de sodio, cloruro de sodio, sulfato de amonio, bicromato de potasio y sulfato de cobre.
          div(titulo="Oxidantes y reductores")
            p Los agentes oxidantes y reductores son oxidantes que, mediante una reaccin qumica, oxidan otras sustancias. El ms utilizado es el ditionito de sodio. Se pueden resaltar en este grupo los siguientes agentes: perxido de hidrgeno (agua oxigenada), hipoclorito de sodio y clorito de sodio.
    .titulo-icono.mb-4(data-aos="fade-right")
        .titulo-icono__texto: h4.text-white.mb-0 El potencial de hidrgeno (pH) 
    .row.justify-content-center.mb-3
      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-right")
        p Constantemente, se escucha hablar en la cotidianidad del pH. Abordndose desde una perspectiva ms simple, se puede decir que es un medidor que busca analizar la concentracin de iones de hidrgeno. El pH es una medida de acidez o alcalinidad de una disolucin. 
      .col-lg-8(data-aos="fade-left")
        .d-flex.flexibilidad.justify-content-center(style="background-color:#D9FBFC")
            img(src='@/assets/curso/temas/tema2/36.svg', alt='' style="width:314px")
            .textos.p-4.m-auto
              p.fw-bold =   +
                br
                br
                |Analice la siguiente concentracin:
                br
                |[ +]=  (.)
                br
                |Aplicando la frmula anterior, el resultado sera un pH de 7, ya que: = 
