      .col-lg-4.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img.d-lg-block.d-none(src='@/assets/curso/temas/tema2/37.png', alt='' style="width: 100%")
      .col-lg-8(data-aos="fade-left")
        p Aparecen tres trminos importantes en lo relacionado con el potencial de hidrgeno (pH) y son los siguientes:
        ul.lista-ul--color
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            |cido (menor a 7)
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            |Neutro (valor 7)
          li.d-flex.mb-1
            i.fas.fa-tshirt(style='color: #FF6E33')
            |Alcalino (mayor a 7)
        p Los nmeros entre parntesis hacen referencia al rango en la escala de valores de pH segn la sustancia. 
          br
          br
          | Estos se pueden medir por tiras indicadoras y peachmetros, as:
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 1
          span Rango en la escala de valores de pH
