    p.mb-5(data-aos='fade-right') Los productos auxiliares son creados como complemento para mejorar los procesos de tintorera, como la limpieza, uniformidad del color, arrugas, pH, impregnacin del color, entre otras funciones ms. Estos pueden poseer, o no, cargas elctricas en su composicin, igual que los colorantes. 
