                  td.text-center(style='width: 30%') Humectantes
                  td Facilitan el proceso de saponificacin, donde agentes grasos se unen con bases de pH alto y permiten que el color penetre en la fibra.
                tr
                  td.text-center(style='width: 30%') Igualadores
                  td Ayudan a conservar la uniformidad del tono de color desde el bao de la tintura.
                tr
                  td.text-center(style='width: 30%') Detergentes
                  td Se encargan de la limpieza superficial de suciedades.
                tr
                  td.text-center(style='width: 30%') Antiinmigrantes
                  td En el teido continuo, se utilizan especialmente durante el secado, para evitar que el color migre y, con ello, no se generen veteados y diferencias en la intensidad del color.
                tr
                  td.text-center(style='width: 30%') Emulsionantes
                  td Eliminan grasas y aceites.
                tr
                  td.text-center(style='width: 30%') Secuestrantes
                  td Mediante procesos para eliminar residuos de metales pesados, en especial con fibras vegetales y sintticas, se utilizan estos productos.
                tr
                  td.text-center(style='width: 30%') Antiquiebres
                  td Previenen arrugas y daos mecnicos. 
                tr
                  td.text-center(style='width: 30%') Mejoradores de solidez
                  td Es utilizado despus de la tintura; tiene dos fines diferentes, el primero, donde retira residuo de colorantes, o fijacin inica con molculas directamente en la fibra.
                tr
                  td.text-center(style='width: 30%') Dispersantes
                  td Permite impregnar y mantener desde el bao de color.
                tr
                  td.text-center(style='width: 30%') Buffers (tampones)
                  td Controlan el valor ptimo necesario de pH en el material y en el bao de tintura.
                tr
                  td.text-center(style='width: 30%') Antiespumantes
                  td Provoca humectacin en el material sin generar espuma.
