        p Por otra parte, los #[span.color-acento-contenido.fw-bold blanqueadores ptimos] son productos que absorben la radiacin en el espectro ultravioleta (no visible para los humanos) y la devuelven a un color visible en el rango azul-violeta. Se utilizan para conseguir un mayor grado de blancura, y su contenido en el sustrato oscila entre 0,01 y 0,5 % spm. La sensibilidad al pH plantea la mayora de los problemas, porque tienden a amarillear en medios alcalinos, por lo que deben neutralizarse cuando el pH es superior a 5, generalmente con cido actico. 
