        p Este #[span.color-acento-contenido.fw-bold blanqueamiento ptico] se realiza sobre fibras naturales para eliminar los pigmentos que las vuelven amarillas; oxidantes como NaClO o H2O2 cambiarn de color y eliminarn la mayora de los colorantes sintticos y naturales. Sin embargo, el grado de blancura obtenido por blanqueamiento qumico incluye un tinte amarillento plido, no se puede obtener verdaderamente blanco. 
    p(data-aos="fade-right") En algunos casos, es necesario aplicar un segundo proceso de blanqueo qumico para reducir el amarilleo residual del sustrato. La colorimetra nos dice que debemos utilizar el azul porque es el contrario. La solucin es desarrollar sustancias fluorescentes que absorban el espectro ultravioleta y emitan luz visible en el espectro azul. El uso de estos agentes blanqueadores puede mejorar el tono azul, sin prdida de brillo, porque el ojo humano no puede percibir la regin ultravioleta absorbida.
